.card__container {
  width: 20rem;
  height: 30rem;
  margin-right: 3rem;
}

.card__item {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 500px) {

  .card__container {
    width: 14rem;
    height: 20rem;
    margin-right: 0;
  }

  .card__item {
    background-size: cover;
  }

}